import React from 'react';
import Chart from 'react-apexcharts';
import { IoMdClose } from "react-icons/io";
import { yearsArray } from '../helpers/functions';

const TimeSeriesCharts = ({ handleShowTimeseries, selectedDistrict, selectedTehsil, selectedMapData, selectedTehsilID,  selectedVariable ,startYear ,endYear}) => {

    const filteredData = selectedMapData.Data.find(item => item.ID === selectedTehsilID);


    const startYearIndex = parseInt(startYear, 10);
    const endYearIndex = parseInt(endYear, 10);

    const years = yearsArray.slice(startYearIndex, endYearIndex + 1);
    const data1 = filteredData[selectedVariable.value].slice(startYearIndex, endYearIndex + 1);
    const data2 = filteredData["pcp_trendline"].slice(startYearIndex, endYearIndex + 1);


    const seriesData = [
        {
            name: selectedVariable.name,
            data: data1,
            type: 'bar' // This could be 'line' or 'bar' depending on how you want to display each series
        },
        {
            name: 'Trendline', // Give this data series a name
            data: data2,
            type: 'line', // This could be 'line' or 'bar' depending on how you want to display each series

        }
    ];


    return (
        <>
            <div className='timeseries_heading'>
                <h4>
                    {selectedDistrict} - {selectedTehsil}<br/>
                    {selectedVariable.name} ({selectedVariable.unit})
                </h4>
                <button className='chart_close_btn' onClick={handleShowTimeseries}><IoMdClose /></button>
            </div>

            <Chart
                options={{
                    chart: {
                        type: 'line', // Overall chart type set to 'line', but each series can override this
                        stacked: false,
                        toolbar: {
                            show: false
                        },
                        zoom: {
                            enabled: false
                        },
                    },
                    plotOptions: {
                        bar: {
                            dataLabels: {
                                enabled: false
                            }
                        }
                    },
                    dataLabels: {
                        enabled: false
                    },
                    tooltip: {
                        enabled: true,
                        theme: 'dark',  // Enhances visibility
                        y: {
                            formatter: (value) => `${value} ${selectedVariable.unit}` // Formats tooltip values
                        }
                    },
                    xaxis: {
                        categories: years,
                        title: {
                            text: 'Years',
                            offsetX: 10
                        }
                    },
                    yaxis: {
                        title: {
                            text: `${selectedVariable.name} (${selectedVariable.unit})`,
                            offsetY: 10
                        }
                    },
                    responsive: [{
                        breakpoint: 480,
                        options: {
                            legend: {
                                position: 'bottom',
                                offsetX: -10,
                                offsetY: 0
                            }
                        }
                    }]
                }}
                series={seriesData}
                type="line" // Default type, but each series specifies its own
                width="100%"
                height="400px"
            />
        </>
    );
};

export default TimeSeriesCharts;
