import React from 'react';
import Chart from 'react-apexcharts';
import { IoMdClose } from "react-icons/io";
import { yearsArray } from '../helpers/functions';

const ExtremesConditionsCharts = ({ handleShowTimeseries, selectedDistrict, selectedTehsil, selectedMapData, selectedTehsilID, selectedVariable,startYear,endYear }) => {

    const filteredData = selectedMapData.Data.find(item => item.ID === selectedTehsilID);


    const startYearIndex = parseInt(startYear, 10);
    const endYearIndex = parseInt(endYear, 10);

    const years = yearsArray.slice(startYearIndex, endYearIndex + 1);
    const data = filteredData[selectedVariable.value].slice(startYearIndex, endYearIndex + 1);


    const seriesData = [{
        name: "Frequency",
        data: data
    }];


    return (
        <>
            <div className='timeseries_heading'>
                {selectedVariable.value === "pcp" ? (
                    <p>
                        {selectedDistrict} - {selectedTehsil}<br />
                        Frequency of extreme precipitation
                        (exceeding 95th percentile rainy days threshold)
                    </p>

                ) : selectedVariable.value === "temp" ? (
                    <p>
                        {selectedDistrict} - {selectedTehsil}<br />
                        Frequency of extreme temperature 
                        (exceeding 95th percentile threshold of temperature for March-May)
                    </p>

                ) : null}


                <button className='chart_close_btn' onClick={handleShowTimeseries}><IoMdClose /></button>
            </div>

            <Chart
                options={{
                    chart: {
                        type: 'bar',
                        stacked: false,
                        zoom: {
                            type: 'x',
                            enabled: true,
                            autoScaleYaxis: true
                        },
                        toolbar: {
                            autoSelected: 'zoom'
                        },
                        dataLabels: {
                            enabled: false
                        },
                    },
                    xaxis: {
                        categories: years,
                        title: {
                            text: 'Years',
                            offsetX: 10
                        }
                    },
                    yaxis: {
                        title: {
                            text: "Frequency",
                            offsetY: 10
                        }
                    },
                    responsive: [{
                        breakpoint: 480,
                        options: {
                            legend: {
                                position: 'bottom',
                                offsetX: -10,
                                offsetY: 0
                            }
                        }
                    }]
                }}
                series={seriesData}
                type="bar"
                width="100%"
                height="400px"
            />
        </>
    );
};

export default ExtremesConditionsCharts;
