import React from 'react';
import Chart from 'react-apexcharts';
import { IoMdClose } from "react-icons/io";
import { yearsArray } from '../helpers/functions';

const ClimatologyTimeSeriesChart = ({ handleShowTimeseries, selectedDistrict, selectedTehsil, selectedMapData, selectedTehsilID, selectedVariable }) => {


    const filteredData = selectedMapData.Data.find(item => item.ID === selectedTehsilID);


    // const startYearIndex = parseInt(startYear, 10);
    // const endYearIndex = parseInt(endYear, 10);

    // const years = yearsArray.slice(startYearIndex, endYearIndex + 1);
    // const data = filteredData[selectedVariable.value].slice(startYearIndex, endYearIndex + 1);


    const seriesData = [{
        name: selectedVariable.name,
        data: filteredData[selectedVariable.value]
    }];


    return (
        <>
            <div className='timeseries_heading'>
                <h4>
                    {selectedDistrict} - {selectedTehsil}<br/>
                    {selectedVariable.name} ({selectedVariable.unit})
                </h4>
                <button className='chart_close_btn' onClick={handleShowTimeseries}><IoMdClose /></button>
            </div>

            <Chart
                options={{
                    chart: {
                        type: 'line',
                        stacked: false,
                        zoom: {
                            type: 'x',
                            enabled: true,
                            autoScaleYaxis: true
                        },
                        toolbar: {
                            autoSelected: 'zoom'
                        },
                        dataLabels: {
                            enabled: false
                        },
                    },
                    xaxis: {
                        categories: yearsArray,
                        title: {
                            text: 'Time',
                            offsetX: 10
                        }
                    },
                    yaxis: {
                        title: {
                            text: `${selectedVariable.name} (${selectedVariable.unit})`,
                            offsetY: 10
                        }
                    },
                    responsive: [{
                        breakpoint: 480,
                        options: {
                            legend: {
                                position: 'bottom',
                                offsetX: -10,
                                offsetY: 0
                            }
                        }
                    }]
                }}
                series={seriesData}
                type="line"
                width="100%"
                height="400px"
            />
        </>
    );
};

export default ClimatologyTimeSeriesChart;
